.form {
  display: flex;
  align-items: center;
  width: 60%;
  padding: 0;
  margin: 0;
  min-height: 100%;
  color: inherit;
}

.input {
  width: 40%;
  height: 40px;
  background-color: rgba(34, 16, 16, 0.411);
  outline: black;
  border: 1px solid white;
  border-radius: 10px;
  padding: 3px 10px;
  color: inherit;
}

::-webkit-search-cancel-button {
  cursor: pointer;
}

::placeholder {
  color: inherit;
}

@media screen and (max-width: 700px) {
  .form {
    width: 90%;
    padding-left: 10px;
  }
  .input {
    width: 100%;
  }
}
